* {

  padding: auto;
  margin: auto;

}

body {

  background-color: rgb(41, 41, 41);

}

h1 {

  color: rgb(255, 255, 255);

  font-family: 'Open Sans', sans-serif;
  font-variant: small-caps;
  text-shadow: black 2px 2px ;
  font-size: 50px;

}

h2 {

  color: rgb(255, 255, 255);

  font-family:  'Open Sans', sans-serif;
  font-weight: 200;
  text-shadow: black 2px 2px;
  text-align: right;

  margin: 10px 0px 40px 0px;

}

p {

  color: white;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  text-shadow: black 1px 1px;
  text-align: justify;

}

a {

 text-decoration: none;
 
}

a:hover {

  color:aqua;
 
 }

.flex {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

.flex-row {
  flex-direction: row;
}


.main-div {
  width: 90vw;
  height: 100vh;

  box-shadow: 0 30px 40px rgba(0,0,0,.5);
  
  background-color: rgb(51, 51, 51);  

}

.main-content-div {

  padding: 0 10% 0 10%;

}

.box-fight {

  width: 150px;
  height: 150px;

  background-color: violet;
  border-radius: 5%;
  border:rgb(122, 0, 0) solid 2px;

  filter: grayscale(100%);

  contain: content;

  margin: 5px;
}

.box-fight:hover {

  filter: grayscale(0%);
  transition: .8s ease;

}

